import React, { useEffect, useState } from "react";
import { TextField, Container, Button, Paper } from "@mui/material";
import Grid from "@material-ui/core/Grid";
import { db } from "../../config/firebaseConfig";
import { connect } from "react-redux";
import moment from "moment";
import "firebase";

const EmployeeAttendance = ({ employeeDetails, users, uid }) => {
  const [inTime, setInTime] = useState("");
  const [outTime, setOutTime] = useState("");
  const [showTime, setShowTime] = useState(false);
  const [attendanceId, setAttendanceId] = useState("");
  const [present, setPresent] = useState(false);
  const [error, setError] = useState("");
  const [currentEmployee, setCurrentEmployee] = useState({});
  const [todayAttendance, setTodayAttendance] = useState({});
  const code = employeeDetails.code,
    empName = employeeDetails.name,
    store = employeeDetails.store,
    handleLogout = employeeDetails.handleLogout,
    attendance = employeeDetails.attendance;
  const date = moment().format("DD/MM/YYYY");
  const date1 = "30/07/2022";
  const time = moment().valueOf();

  useEffect(() => {
    var temp = [];
    db.collection("attendance")
      .where("date", "==", date)
      .get()
      .then((snapshot) => {
        snapshot.docs.forEach((doc) => {
          temp.push(doc.data());
          const data = doc.data();
          setAttendanceId(doc.id);
          setTodayAttendance(data);
          if (data[empName]) {
            setPresent(data[empName].present);
            setCurrentEmployee(data[empName]);
            setInTime(data[empName].inTime);
            setPresent(data[empName].present);
          }
        });
      });
  }, []);

  function handleInTime() {
    setShowTime(true);
    setInTime(moment().format("LT"));
    setTodayAttendance({
      ...todayAttendance,
      // ...[empName],
      [empName]: {
        id: code,
        name: empName,
        inTime: time,
        store: store,
        present: true,
        date: date,
      },
      date: date,
    });
  }

  function handleOutTime() {
    var a = new Date(time);
    var b = new Date(inTime);

    var result = (a.getTime() - b.getTime()) / 1000;
    console.log(result);
    let hours = Math.floor(result / 3600);
    let minutes = Math.floor((result - hours * 3600) / 60);
    let totalHours = hours + ":" + minutes;

    if (present) {
      setShowTime(true);
      setOutTime(moment().format("LT"));
      setTodayAttendance((prev) => ({
        ...prev,
        [empName]: {
          ...currentEmployee,
          outTime: time,
          totalHours: totalHours,
        },
      }));
    } else {
      setError("In Time not Recorded!");
    }
  }

  function handleUpdate() {
    attendanceId === ""
      ? db
          .collection("attendance")
          .add(todayAttendance)
          .then(console.log("adding new array"))
      : db
          .collection("attendance")
          .doc(attendanceId)
          .set(todayAttendance)
          .then(console.log("updating the array"));
    // handleLogout();
  }
  return (
    <Paper>
      <div style={{ padding: "30px" }}>
        <Container component="main" maxWidth="xs">
          <div>
            <Button
              style={{
                position: "relative",
                padding: "10px",
                top: "-10px",
                float: "right",
                // boxShadow: "1px 1px 1px 1px lightgrey",
              }}
              onClick={handleLogout}
            >
              {empName}, LogOut
            </Button>

            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              type="number"
              name="empId"
              label="Employee ID"
              value={code}
              disabled
            />
            <TextField
              disabled
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="Employee Name"
              label="Employee Name"
              value={empName}
              // onChange={handleChange}
            />
            <TextField
              id="outlined-select-currency"
              fullWidth
              label="Store"
              value={store}
              defaultValue={store}
              disabled
              // onChange={handleChange}
            />

            <TextField
              disabled
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="Date"
              label="Date"
              value={date}
              // onChange={handleChange}
            />

            <div
              style={{
                display: "flex",
                flexDirection: "row",
                paddingTop: "10px",
              }}
            >
              <TextField
                disabled
                variant="outlined"
                margin="normal"
                required
                name="name"
                label="In-Time"
                value={present ? moment(inTime).format("LT") : ""}
                // onChange={handleChange}
              />
              <TextField
                disabled
                variant="outlined"
                margin="normal"
                required
                // name="name"
                label="Out-Time"
                placeholder=""
                value={outTime}
                // onChange={handleChange}
              />
            </div>
          </div>

          <Grid
            container
            justifyContent="space-between"
            style={{ textAlign: "center", marginTop: "20px" }}
          >
            <Button
              variant="contained"
              color="primary"
              style={{
                backgroundColor: "#3F50B5",
              }}
              onClick={handleInTime}
            >
              In-Time
            </Button>

            <Button
              variant="contained"
              color="primary"
              style={{
                backgroundColor: "#3F50B5",
              }}
              onClick={handleOutTime}
            >
              Out-Time
            </Button>
          </Grid>
          <Grid style={{ marginTop: "30px", marginBottom: "30px" }}>
            <Button
              variant="contained"
              color="primary"
              style={{
                backgroundColor: "#3F50B5",
              }}
              onClick={handleUpdate}
            >
              Submit
            </Button>
          </Grid>
          <i style={{ color: "red" }}>{error}</i>
          {showTime && (
            <i>
              Attendance Updated <br />
              Time : {time}
            </i>
          )}
        </Container>
      </div>
    </Paper>
  );
};
const mapStateToProps = (state) => {
  return {
    uid: state.firebase.auth.uid,
    users: state.firestore.ordered.users,
  };
};

export default connect(mapStateToProps)(EmployeeAttendance);
