import React, { useEffect, useState } from "react";
import { signUp } from "../../actions/auth";
import SignIn from "../auth/SignIn";
import { connect } from "react-redux";
import EmployeeSignIn from "./EmployeeSignIn";

import EmployeeDetails from "./EmployeeDetails";
const Employee = ({ state, attendance }) => {
  const users = state.users,
    uid = state.uid;
  const employeeLogin = true;
  const [admin, setAdmin] = useState();
  const EmployeeContext = React.createContext();
  // var userDetailContext = React.createContext(null);
  useEffect(() => {
    for (var i in users) {
      if (uid === users[i].id) {
        setAdmin(users[i].admin);
      }
    }
  });

  return (
    <EmployeeContext.Provider value={attendance}>
      <div>
        {admin ? (
          <EmployeeDetails state={state} attendance={attendance} />
        ) : (
          <EmployeeSignIn state={state} attendance={attendance} />
        )}
      </div>
    </EmployeeContext.Provider>
  );
};
const mapStateToProps = (state) => {
  return {
    uid: state.firebase.auth.uid,
    attendance: state.firestore.ordered.attendance,
  };
};

export default connect(mapStateToProps)(Employee);
