import { TextField, Button, Container, Paper, MenuItem } from "@mui/material";
import React, { useState, useEffect } from "react";
import ForgetPasswordDialog from "./ForgetPasswordDialog";
import { empSignIn } from "../../actions/empAuth";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { firestoreConnect } from "react-redux-firebase";
import { compose } from "redux";
import EmployeeAttendance from "./EmployeeAttendance";
import EmployeeDetails from "./EmployeeDetails";
export const START_EMPLOYEE_ACTION = "START_EMPLOYEE_ACTION";
const EmployeeSignIn = ({ state, attendance }) => {
  // const [empList, setEmpList] = useState([]);
  const [code, setCode] = useState("");
  const [login, setLogin] = useState(false);
  const [empName, setEmpName] = useState("");
  const [emp, setEmp] = useState("");
  const [password, setPassword] = useState();
  const [store, setStore] = useState("");
  const [error, setError] = useState("");
  const empList = state.employees;
  const users = state.users,
    uid = state.uid;

  useEffect(() => {
    // console.log(attendance);
  }, [attendance]);

  const stores = [
    {
      value: "BCC LL",
      label: "BCC LL",
    },
    {
      value: "BCC UL",
      label: "BCC UL",
    },
    {
      value: "EMTC",
      label: "EMTC STORE",
    },
    {
      value: "EMTC CART",
      label: "EMTC CART",
    },
    {
      value: "SQUARE ONE STORE",
      label: "SQUARE ONE STORE",
    },
    {
      value: "TP 1",
      label: "TP 1",
    },
  ];

  useEffect(() => {
    for (var i in empList) {
      empList[i].empId === code && setEmpName(empList[i].name);
    }
  }, [code, empList]);

  useEffect(() => {
    for (var user in users) {
      if (users[user].id === uid) setStore(users[user].firstName);
    }
  }, [users]);

  function handleSubmit() {
    for (var i in empList) {
      if (empList[i].empId === code && empList[i].password === password) {
        setLogin(true);
        setEmp(empList[i].name);
      } else {
        setError("* Please enter correct password");
      }
    }
  }
  function handleLogout() {
    setCode("");
    setPassword("");
    setError();
    setLogin(false);
  }
  function handlestore(e) {
    setStore(e.target.value);
    console.log(e.target.value);
  }
  useEffect(() => {
    // console.log(state.empLoading);
  }, [state.empLoading]);

  const employeeDetails = {
    code: code,
    name: empName,
    store: store,
    handleLogout: handleLogout,
    attendance: attendance,
  };

  return (
    <div>
      {!login ? (
        <Container component="main" maxWidth="xs">
          <Paper>
            <div
              style={{
                margin: "50px",
                paddingTop: "40px",
                paddingBottom: "40px",
              }}
            >
              <h2>Employee Login</h2>

              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                type="number"
                name="empId"
                label="Employee ID"
                value={code}
                onChange={(e) => setCode(e.target.value)}
              />
              <TextField
                disabled
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="name"
                label="Employee Name"
                value={empName}
                // onChange={handleChange}
              />
              <TextField
                id="outlined-select-currency"
                select
                fullWidth
                label="Store"
                value={store}
                style={{ top: "10px" }}
                defaultValue={store}
                onChange={handlestore}
              >
                {stores.map((option) => (
                  <MenuItem key={option.value} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}
              </TextField>

              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                type="password"
                name="Enter the Password"
                label="Enter the Password"
                value={password}
                style={{ top: "10px" }}
                onChange={(e) => setPassword(e.target.value)}
                inputProps={{
                  autoComplete: "new-password",
                  form: {
                    autoComplete: "off",
                  },
                }}
              />

              <Button
                variant="contained"
                color="primary"
                type="submit"
                style={{
                  backgroundColor: "#3F50B5",
                  top: "10px",
                }}
                onClick={handleSubmit}
              >
                Login
              </Button>

              <i style={{ color: "red" }}>{error}</i>
              <div style={{ top: "10px" }}>
                <ForgetPasswordDialog />
              </div>
            </div>
          </Paper>
        </Container>
      ) : (
        <EmployeeAttendance employeeDetails={employeeDetails} />
      )}
    </div>
  );
};

export default EmployeeSignIn;
