import React, { useState, useEffect } from "react";
import { db } from "../../config/firebaseConfig";
import { TextField, Container, Button } from "@mui/material";
const ForgetPassword = ({ handleClose }) => {
  const [employee, setEmployee] = useState({
    empId: "",
    name: "",
    password: "",
  });
  const [empList, setEmpList] = useState();
  const [code, setCode] = useState("");
  const [empName, setEmpName] = useState("");
  const [current, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState();
  const [id, setId] = useState("");
  const [error, setError] = useState("");
  useEffect(() => {
    fetchEmp();
  }, []);

  const fetchEmp = async () => {
    const response = db.collection("employees");
    const data = await response.get();
    let temp = [];
    data.docs.forEach((item) => {
      temp.push(item.data());
    });
    // console.log(temp);
    setEmpList(temp);
  };
  function handleChange(e) {
    var value = e.target.value;
    setEmployee((prev) => ({
      ...prev,
      empId: code,
      name: empName,
      password: value,
    }));
    console.log(employee);
  }

  useEffect(() => {
    db.collection("employees")
      .get()
      .then((snapshot) => {
        snapshot.docs.forEach((doc) => {
          for (var i in empList) {
            if (doc.data().empId === code) {
              setId(doc.id);
            }
          }
        });
      });
    for (var i in empList) {
      if (empList[i].empId === code) {
        setEmpName(empList[i].name);
      }
    }
  }, [code]);

  function handleText(e) {
    setCode(e.target.value);
  }
  function handlePassword(e) {
    setCurrentPassword(e.target.value);
  }
  function handleSubmit() {
    for (var i in empList) {
      if (empList[i].empId === code && empList[i].password === current) {
        resetPassword(id);
        handleClose();
      } else {
        setError("* enter correct password");
      }
    }
  }
  function resetPassword(id) {
    db.collection("employees").doc(id).set(employee);
  }

  return (
    <Container component="main" maxWidth="xs">
      <div>
        <form autoComplete="off">
          <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            autoComplete="off"
            name="empId"
            label="Employee ID"
            value={code}
            onChange={handleText}
          />
        </form>
        <TextField
          disabled
          variant="outlined"
          margin="normal"
          required
          fullWidth
          name="name"
          label="Employee Name"
          value={empName}
          // onChange={handleChange}
        />
        <TextField
          variant="outlined"
          margin="normal"
          type="password"
          required
          fullWidth
          name="Current "
          label="Current "
          value={current}
          onChange={handlePassword}
        />
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          name="New Password"
          label="New Password"
          value={newPassword}
          onChange={handleChange}
        />

        <Button
          variant="contained"
          color="primary"
          style={{
            backgroundColor: "#3F50B5",
          }}
          onClick={handleSubmit}
        >
          Reset Password
        </Button>

        <i style={{ color: "red" }}>{error}</i>
      </div>
    </Container>
  );
};

export default ForgetPassword;
